Link {
    text-decoration: 'none';
    color: 'rgb(82, 80, 80)';
}

.log-buttons {
    display: flex;
    justify-content: center;

    @media only screen and (min-width: 1281px) {
        justify-content: flex-end;
        margin-right: 10em;
        padding-bottom: 10px;
        padding-top: 10px;

        &__element {
            padding: 10px;
            border: 1px solid transparent;
            margin-left: 10px;
            margin-right: 10px;

            &--account {
                margin-left: 10px;
                margin-right: 10px;
                padding: 10px;
                border: 1px solid yellow;
            }

            &--account:hover {
                border: 1px solid black;
            }
        }

        &__element:hover {
            border: 1px solid black;
            cursor: pointer;
            color: black;
        }
    }

    a {
        text-decoration: none;
        color: rgb(82, 80, 80);
    }

    @media only screen and (min-width: 551px) and (max-width: 1280px) {
        justify-content: center;
        padding-top: 1em;

        &__element {
            padding: 15px;
            border: 1px solid rgb(82, 80, 80);
            margin-left: 10px;
            margin-right: 10px;
            font-weight: 600;

            &--account {
                margin-left: 10px;
                margin-right: 10px;
                padding: 15px;
                border: 1px solid rgb(82, 80, 80);
                background-color: rgb(254, 209, 0);
                font-weight: 600;
            }
        }
    }

    @media only screen and (max-width: 550px) {
        padding-top: 0.5em;

        &__element {
            padding: 20px;
            margin-left: 10px;
            margin-right: 10px;
            font-weight: 500;
            border: 1px solid rgb(82, 80, 80);

            &--account {
                margin-left: 10px;
                margin-right: 10px;
                padding: 20px;
                border: 1px solid rgb(82, 80, 80);
                background-color: rgb(254, 209, 0);
                font-weight: 600;
            }
        }
    }
}